@media(min-width : 300px) {

    .f-head-pack {
        display: flex;
        flex-flow: row;

        position: sticky;
        top: 1px;

        margin: 5px;
        padding: 5px;

        border: 2px solid saddlebrown;
        background-color: wheat;

        z-index: 1;
    }

    .head-heading {
        font-size: 2.5rem;

        border-left: 2px solid black;
        margin-left: 5px;
    }

    .f-head-packlarge {
        display: flex;
        flex-flow: column;

        position: sticky;
        top: 1px;

        margin: 5px;
        padding: 5px;

        border: 2px solid saddlebrown;
        background-color: wheat;

        z-index: 1;
    }


    .f-pack {
        display: flex;
        width: 100vw;
    }

    .f-contain {
        background-color: rgb(9, 22, 59);
        width: 98vw;
        height: 80vh;

        position: relative;

        margin: 5px;
        padding: 5px;
        padding-bottom: 2.5rem;

        border: 2px solid black;
        border-radius: 10px;

        display: flex;
        flex-flow: column;
        justify-content: center;





    }

    .f-item-pack {
        display: flex;
        flex-flow: column wrap;



    }

    .f-item-pack button {
        font-size: 1.2rem;
        font-family: cursive;

        padding: 15px;
        margin: 5px;

        width: fit-content;

        border: 1px solid saddlebrown;
        border-radius: 5px;

        background-color: wheat;
    }

    .f-words {

        font-size: 1.6rem;

        font-family: cursive;
        font-weight: 600;
        margin: 5px;
        margin-bottom: 2.5rem;

        color: wheat;

    }

    .f-words2 {

        font-size: 2.0rem;

        font-family: cursive;
        font-weight: 600;
        margin: 5px;
        margin-bottom: 2.5rem;

        color: wheat;

        align-self: flex-end;
    }

    .blober {
        display: inline;

        margin: 5px;
        padding: 5px;

        background-color: wheat;

        color: black;

        border-radius: 10px;
    }

    .f-vitem {
        width: 120vw;
        position: absolute;
    }

    .nav-pack {
        position: relative;
    }

    .nav-contain {
        background-color: sandybrown;
        border: 2px solid black;

        height: 80vh;
        margin: 5px;
        position: fixed;

        overflow: auto;

        top: 4rem;

        z-index: 1;
    }

    .nav-pack-mode2{
        display: flex;
        flex-flow: row;

        margin: 5px;
    }

    .nav-pack-mode2 button{
        margin: 5px;
    }

    

    .footer {
        margin: 5px;
        padding: 5px;

        display: flex;
        flex-flow: row;

        justify-content: center;

        background-color: wheat;

        border: 2px solid saddlebrown;
    }

    .footer div{
        margin: 5px;
        display: flex;

        flex-flow: column;
    }

}

@media(min-width : 601) {
    .f-head-pack-large {
        display: flex;
        flex-flow: column;
    }

}