.login-pack{
    display: flex;
    flex-flow: row;

    
    justify-content: center;

  
    margin: 5px;
}

.login-contain{
    padding: 10px;
    display: flex;
    flex-flow: column wrap;

    width: 350px;
    

    align-content: center;

    border: 2px solid black;

    background-color: wheat;
}
.login-contain h1{
    border-bottom: 3px solid saddlebrown;
}
.h-log{
    margin: 5px;
    font-size: 1.3rem;

    font-family: cursive;
}

.login-form{
    margin: 5px;

    font-family: cursive;
}

.form-submit{
    margin-top: 25px;
    font-size: 1.2rem;

    font-family: cursive;

    color: wheat;
    background-color:rgb(9, 22, 59);
}

.form-input1, .form-input2{
    border: initial;

    background-color: wheat;
    border-bottom: 2px solid black;
}